@import "style/mixins.module.scss";

.root {
    background: linear-gradient(180.18deg, #fff1f2 12.87%, #eef3f4 99.84%);
    padding: 64px 0;

    @include respond-to("laptop") {
        background: linear-gradient(180.18deg, #fff1f2 41.61%, #eef3f4 99.84%);
        padding: 115px 0;
    }

    @include respond-to("desktop") {
        padding: 155px 0;
    }
}

.header {
    margin-bottom: 64px;

    @include respond-to("laptop") {
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
        align-items: center;

        margin-bottom: 115px;
    }

    @include respond-to("desktop") {
        margin-bottom: 155px;
    }
}

.button {
    display: none;

    @include respond-to("laptop") {
        display: inline-block;
    }
}

.content {
    @include respond-to("laptop") {
        margin-right: 40px;
    }
}

.title {
    margin-bottom: 0;
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;
    @include color("gray", "-7");

    &_invisible {
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: get_color("gray", "-4");
    }
}

.description {
    max-width: 460px;
    margin-bottom: 0;
    font-weight: 400;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        margin-bottom: 40px;

        font-size: 22px;
        line-height: 32px;
    }
}

.image {
    max-width: 363px;
    width: 100%;
    margin-bottom: 64px;

    @include respond-to("laptop") {
        display: none;
    }
}

.video {
    width: 100%;
    height: auto;
    margin-bottom: 64px;

    @include respond-to("laptop") {
        max-width: 480px;
        margin-bottom: 0;
    }
}
