@import "style/mixins.module.scss";
@import "./animateImages.module.scss";
@import "./animateText.module.scss";

.root {
    overflow-x: hidden;
    background-color: get_color("gray", "0");

    @include respond-to("laptop") {
        padding-bottom: 137px;
    }

    @include respond-to("desktop") {
        padding-bottom: 257px;
    }
}

.header {
    margin-bottom: 48px;

    @include respond-to("laptop") {
        margin-bottom: 64px;
    }

    @include respond-to("desktop") {
        margin-bottom: 72px;
    }
}

.button-fixed {
    text-transform: uppercase;
}

.title {
    margin-bottom: 32px;
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;
    @include color("gray", "-7");
    text-align: left;

    &__text-red-box {
        height: 2.1em;

        margin-bottom: 0;
        display: block;
        position: relative;

        color: #db688c;

        @include respond-to("laptop") {
            height: 1.2em;
        }
    }

    &__text-red {
        text-align: left;
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;
        animation-duration: 16s;
        animation-iteration-count: infinite;
        animation-fill-mode: forwards;
        animation-timing-function: cubic-bezier(0.42, 0, 0, 1);
        animation-delay: 1s;

        &:first-child {
            animation-name: fadeUp1;
        }

        &:nth-child(2) {
            animation-name: fadeUp2;
            transform: translate3d(0, -50%, 0);
            opacity: 0;
        }

        &:nth-child(3) {
            animation-name: fadeUp3;
            transform: translate3d(0, -50%, 0);
            opacity: 0;
        }

        &:nth-child(4) {
            animation-name: fadeUp4;
            transform: translate3d(0, -50%, 0);
            opacity: 0;
        }

        @include respond-to("laptop") {
            text-align: center;
        }
    }

    @include respond-to("laptop") {
        text-align: center;
        margin-bottom: 64px;
        width: 800px;
        margin-left: auto;
        margin-right: auto;
    }

    @include respond-to("desktop") {
        margin-bottom: 73px;
    }
}

.container {
    @include respond-to("laptop") {
        display: flex;
        justify-content: flex-end;
        flex-direction: row-reverse;
        align-items: center;
    }
}

.subtitle {
    margin-bottom: 8px;
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;
    @include color("gray", "-7");
}

.text {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 17px;
    line-height: 24px;
    margin-bottom: 0;

    @include respond-to("laptop") {
        margin-bottom: 16px;
    }

    @include respond-to("desktop") {
        font-size: 22px;
        line-height: 28px;
    }
}

.container-images {
    position: relative;
    height: calc(100vw - 48px);
    margin-bottom: 80px;
    max-height: 600px;

    width: 100%;
    left: calc(50%);
    transform: translateX(-50%);

    @include respond-to("laptop") {
        margin-left: 0;
        margin-right: 0;
        left: auto;
        transform: none;
        width: 460px;
        height: 488px;
        max-height: auto;
        margin-bottom: 0;
    }

    @include respond-to("desktop") {
        width: 624px;
        height: 663px;
    }

    picture {
        height: 100%;
    }
}

.container-text {
    @include respond-to("laptop") {
        width: 300px;
        margin-right: 82px;
    }
}

.image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transform-origin: 25% bottom;
    animation-duration: 16s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.42, 0, 0, 1);
    object-fit: cover;
    z-index: auto;

    @include respond-to("laptop") {
        width: 460px;
        height: 488px;
    }

    @include respond-to("desktop") {
        width: 624px;
        height: 663px;
        transform-origin: 22% bottom;
    }

    &_1 {
        transform: rotate(15deg) translateX(100px);
        opacity: 0.3;
        animation-name: fadeImage1;

        @include respond-to("desktop") {
            transform: rotate(15deg) translateX(72px);
        }
    }

    &_2 {
        transform: rotate(10deg) translateX(100px);
        opacity: 0.5;
        animation-name: fadeImage2;

        @include respond-to("desktop") {
            transform: rotate(10deg) translateX(72px);
        }
    }

    &_3 {
        transform: rotate(5deg) translateX(100px);
        opacity: 0.8;
        animation-name: fadeImage3;

        @include respond-to("desktop") {
            transform: rotate(5deg) translateX(72px);
        }
    }

    &_4 {
        transform: rotate(0) translateX(0);
        opacity: 1;
        animation-name: fadeImage4;
    }
}

.button {
    display: none;

    @include respond-to("laptop") {
        display: inline-block;
    }
}
