@import "style/mixins.module.scss";

.root {
    font-family: Inter, sans-serif;
    font-weight: 400;
    @include color("gray", "-7");
}

.head {
    @include respond-to("mobile") {
        margin-bottom: 120px;
    }
}

.form-header {
    max-width: 800px;
    margin: 0 auto 30px;

    @include respond-to("laptop") {
        margin-bottom: 50px;
    }
}

.form-title {
    font-size: 45px;
    line-height: 40px;
    @include color("gray", "-7");
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;

    @include respond-to("laptop") {
        text-align: center;
        font-size: 64px;
        line-height: 64px;
    }
}

.container,
.how-it-works {
    margin-bottom: 120px;

    @include respond-to("desktop") {
        margin-bottom: 240px;
    }
}

.how-it-works {
    @include respond-to("laptop") {
        padding-left: 40px;
        padding-right: 40px;
    }

    @include respond-to("container-default") {
        width: 1288px;
        margin-left: auto;
        margin-right: auto;
    }
}

.appointment {
    margin-bottom: 120px;

    @include respond-to("laptop") {
        margin-bottom: 144px;
    }

    @include respond-to("desktop") {
        margin-bottom: 200px;
    }
}

.doctors {
    margin-bottom: 64px;

    @include respond-to("laptop") {
        margin-bottom: 72px;
    }

    @include respond-to("desktop") {
        margin-bottom: 140px;
    }
}

.our-results-title {
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;
    @include color("gray", "-7");
}

.appointment-title {
    @include formTitle();
}