@import "style/mixins.module";

.payment-monthly {
    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop") {
        margin-bottom: 30px;
        padding-bottom: 16px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    }

    @include respond-to("laptop-L") {
        padding-bottom: 44px;
        font-size: 17px;
        line-height: 24px;
    }

    &__period {
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-size: 20px;
            line-height: 28px;
        }
    }

    &__row {
        display: flex;
        align-items: center;

        @include respond-to("laptop") {
            align-items: normal;
        }
    }

    &__amount {
        margin: 0;
        font-family: "Source Serif Pro", sans-serif;
        font-weight: 700;
        font-size: 36px;
        line-height: 40px;
        @include color("gray", "0");

        @include respond-to("laptop") {
            font-size: 56px;
            line-height: 64px;
        }
    }

    &__currency {
        display: flex;
        flex-direction: column;
        margin-left: 8px;

        @include respond-to("laptop") {
            min-width: 77px;
            margin-left: 16px;
        }
    }

    &__month,
    &__period {
        margin: 0;
    }

    &__month,
    &__from-text {
        font-family: Inter, sans-serif;
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-size: 20px;
            line-height: 28px;
        }
    }

    &__month {
        font-weight: 600;

        @include respond-to("laptop") {
            font-weight: 400;
        }
    }

    &__from-text {
        display: none;

        @include respond-to("laptop") {
            display: block;
            margin: 27px 7px 0 0;
        }
    }
}

@media (max-width: 375px) {
    .payment-monthly__month,
    .payment-monthly__period {
        font-size: 13px;
        line-height: 20px;
    }
}
