@import "style/mixins.module";

.content {
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;

    @include respond-to("mobile") {
        align-items: center;
    }

    @include respond-to("laptop") {
        margin-bottom: 30px;
    }
}

.subtitle {
    display: inline-flex;
    align-items: center;

    @include respond-to("mobile") {
        justify-content: center;
    }

    &__text {
        margin-bottom: 0;
        font-size: 13px;
        line-height: 20px;

        &_red {
            @include color("primary", "red");
        }

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }

    &__label {
        @include color("primary", "red");

        padding: 1.5px 4.5px;
        border: 1px solid get_color("primary", "red");
        border-radius: 12px;
        text-transform: uppercase;
        letter-spacing: 0.12em;
        font-weight: bold;
        font-size: 7px;
        line-height: 7px;

        &_mobile {
            @include respond-to("laptop") {
                display: none;
            }
        }

        &_desktop {
            display: none;

            @include respond-to("laptop") {
                display: inherit;
                margin-left: 10px;
                border-radius: 19px;
                font-size: 10px;
                line-height: 11px;
            }

            @include respond-to("laptop-L") {
                padding: 3px 10px;
                border-radius: 32px;
                font-size: 17px;
                line-height: 24px;
            }
        }
    }
}

.title {
    display: inline-flex;

    @include respond-to("mobile") {
        flex-direction: column;
        align-items: flex-end;
    }

    @include respond-to("laptop") {
        align-items: flex-start;
    }

    &__text {
        margin-bottom: 0;
        font-family: "Source Serif Pro", sans-serif;
        font-weight: 700;
        @include color("gray", "0");

        @media (max-width: 400px) {
            font-size: 28px;
            line-height: 32px;
        }
    }
}
