@import "style/mixins.module.scss";

.root {
    margin: 0;
    padding: 0;
    font-family: Inter, sans-serif;
    font-weight: 400;

    @include respond-to("laptop") {
        display: flex;
    }
}

.icon {
    width: 64px;
    height: 64px;

    margin-bottom: 24px;

    @include respond-to("laptop") {
        width: 150px;
        height: 150px;

        margin-bottom: 40px;
    }

    svg {
        width: 100%;
        height: 100%;
    }
}

.item {
    margin-bottom: 48px;

    &:last-child {
        margin-bottom: 0;
    }

    @include respond-to("laptop") {
        width: 33.333%;
        margin: 0 40px 0 0;

        &:last-child {
            margin: 0;
        }
    }
}

.title {
    margin-bottom: 8px;
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;
    @include color("gray", "-7");
}

.description {
    margin-bottom: 0;

    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}
