@import "style/mixins.module.scss";

$z-index-slide: 20;

.title {
    margin-bottom: 16px;
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;
    @include color("gray", "-7");

    @include respond-to("laptop") {
        margin-bottom: 32px;
        width: 800px;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }
}

.description {
    margin-bottom: 105px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        margin-bottom: 133px;
        width: 800px;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }

    @include respond-to("desktop") {
        font-size: 22px;
        line-height: 32px;
        margin-bottom: 209px;
    }
}

.container-default {
    padding-right: 0px;
    padding-left: 0px;
    position: relative;

    @include respond-to("desktop") {
        padding-left: 40px;
        padding-right: 40px;
    }
}

.container-slider {
    width: 100%;
    overflow: hidden;
    position: relative;

    &::after {
        content: "";

        display: block;
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;
        height: 200px;
        z-index: -1;

        @include respond-to("laptop") {
            height: 320px;
        }
    }
}

.icon {
    position: absolute;
    top: 0;
    z-index: 30;
    transform: translateY(calc(-50% - 12px));
    left: 24px;

    @include respond-to("tablet") {
        left: 40px;
    }

    svg {
        width: 73px;
        height: 73px;

        @include respond-to("laptop") {
            width: 133px;
            height: 133px;
        }
    }
}

.slider {
    overflow-x: scroll;
    display: flex;
    scrollbar-width: none;
    padding-left: 24px;
    padding-right: 24px;

    &::-webkit-scrollbar {
        display: none;
    }

    @include respond-to("desktop") {
        border-radius: 88px;
        padding-left: 0px;
        padding-right: 0px;
    }
}

.slide {
    $slide: &;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 46px;

    @include respond-to("mobile") {
        margin-right: 16px;
    }

    @include respond-to("desktop") {
        min-width: 272px;
        margin-right: 40px;
    }

    &:last-child {
        margin-right: 0;

        #{$slide}__title,
        #{$slide}__description {
            z-index: $z-index-slide;
        }
    }

    &__image {
        object-fit: cover;
        vertical-align: bottom;
        height: 200px;

        @include respond-to("laptop") {
            border-radius: 40px;
            height: 344px;
        }

        @include respond-to("desktop") {
            border-radius: 88px;
        }
    }

    &__picture {
        margin-bottom: 14px;

        @include respond-to("laptop") {
            margin-bottom: 24px;
        }
    }

    &__title {
        margin-bottom: 2px;
        text-align: center;

        @include respond-to("laptop") {
            margin-bottom: 4px;
        }
    }

    &__description {
        text-align: center;
        font-size: 13px;
        line-height: 20px;
        margin-bottom: 0;
        padding-left: 14px;
        padding-right: 14px;

        @include respond-to("laptop") {
            font-size: 17px;
            line-height: 24px;
        }
    }
}

.arrows {
    display: none;

    @include respond-to("desktop") {
        display: flex;
        justify-content: flex-end;
        position: absolute;
        top: 0;
        right: 0;
        padding-right: 40px;
        transform: translateY(calc(-100% - 8px));
    }
}

.arrow {
    width: 56px;
    height: 56px;
    border: 1px solid rgba(66, 78, 88, 0.1);
    border-radius: 2px;
    background-color: transparent;
    cursor: pointer;

    &:first-child {
        margin-right: 14px;
    }

    &:disabled {
        &__icon {
            color: #abb4bb;
        }
    }
}
