@import "style/mixins.module";

.content {
    display: flex;
    flex-direction: column;

    @include respond-to("laptop") {
        flex-direction: row;
    }
}

.price {
    margin-bottom: 0;
    font-family: "Source Serif Pro", sans-serif;
    font-weight: 700;
    white-space: nowrap;
    @include color("gray", "0");
}

.payment-once__from-text {
    text-align: center;
    margin: 0;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        margin: 27px 7px 0 0;
        font-size: 20px;
        line-height: 28px;
    }
}
